import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome'
import { Button } from 'reactstrap';
import axios from 'axios';

import logo from './logo.svg';
import './App.css';

function getQueryVariable(variable) {
  const query = window.location.search.substring(1)
  const vars = query.split('&')
  const code = vars
    .map(i => {
      const pair = i.split('=')
      if (pair[0] === variable) {
        return pair[1]
      }
      return null
    })
    .filter(d => {
      if (d) {
        return true
      }
      return false
    })
  return code[0]
}

class App extends Component {
  constructor() {
    super()
    this.state = {
      user: {},
      client: {
        clientId: 'bd54c430051b4970a6333ea361559121',
        scope: 'basic+public_content+comments+relationships+likes+follower_list',
        clientSecret: 'a010cb2a2c1745ebaeb9094f92d905f6',
        code: ''
        // redirectUri: 'https://dmmeteo.github.io/react-pages/'
      },
      accessToken: ''
    }
  }

  componentWillMount(){
    let token = window.localStorage.getItem('access_token');
    const { clientId, scope, clientSecret, code } = this.state.client
    const redirectUri = this.state.client.redirectUri || window.location.href

    if (token){
      console.log('if token exist')
      this.setState({accessToken: token})
    } else {
      console.log('if token NOT exist')
      if (window.location.search.includes('code')) {
        console.log('if code exist')
        this.setState({
          client: {code: getQueryVariable('code')}
        })
        axios.post('https://api.instagram.com/oauth/access_token', {
            client_id: clientId,
            client_secret: clientSecret,
            grant_type: 'authorization_code',
            redirect_uri: redirectUri,
            code: code,
          })
          .then(function (response) {
            console.log('accessToken: '+response.data);
          })
          .catch(function (error) {
            console.log('accessTokenERROR: '+error);
          });
      } else if (window.location.search.includes('error')) {
        console.log('if error exist')
        console.error('error'+getQueryVariable('error'))
        console.error('error_reason'+getQueryVariable('error_reason'))
        console.error('error_description'+getQueryVariable('error_description'))
      } else {
        console.log('if code NOT exist')
        window.location.href = `https://api.instagram.com/oauth/authorize/?client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scope}&response_type=code`
      }
    }
  }

  componentDidMount(){
    this.getUsers()
  }

  getUsers(){
    // fetch(`https://api.instagram.com/v1/users/self/?access_token=${this.state.accessToken}`)
    //   .then(response => response.json())
    //   .then(data => this.setState({ user: data.data }));
  }

  render() {
    let user = this.state.user;
    console.log(user)
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to React</h1>
        </header>
        <p className="App-intro">
          To get started, edit <code>src/App.js</code> and save to reload.
        </p>
      </div>
    );
  }
}

export default App;



